body {
  margin: 0;
  overflow: hidden;
  font-family: Arial, sans-serif;
}

.app-container {
  position: relative;
  height: 100vh;
  width: 100vw;
}

.toolbar {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255, 255, 255, 0.9);
  padding: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  display: flex;
  gap: 10px;
  z-index: 10;
  border-radius: 5px;
}

.toolbar-button {
  background: #007bff;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s ease;
  font-size: 14px;
}

.toolbar-button:hover {
  background: #0056b3;
}

.sidebar {
  position: absolute;
  top: 70px;
  left: 10px;
  background: rgba(255, 255, 255, 0.9);
  padding: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  gap: 10px;
  z-index: 10;
  border-radius: 5px;
}

.image-selector {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.image-thumbnail {
  width: 60px;
  height: 60px;
  cursor: pointer;
  border: 2px solid transparent;
  border-radius: 5px;
  transition: border 0.3s ease, transform 0.3s ease;
}

.image-thumbnail.selected {
  border: 2px solid #007bff;
  transform: scale(1.1);
}

canvas {
  display: block;
  width: 100%;
  height: 100%;
}
